$black: #181818;
$width: 800px;
$purple: #262161;
$yellow: #ffc40e;
$pink: #ee7297;
$green: #93ff00;
$red: #e50714;
$grey: lighten(grey, 40%);
$shad: 0 0 10px rgba(0, 0, 0, 0.1), 0 5px 10px rgba(0, 0, 0, 0.05);
$grad: linear-gradient(
  90deg,
  #48ded4 0%,
  #a026bf 20%,
  #e82c75 60%,
  $yellow 85%,
  #48ded4 95%
);
$verticalGrad: linear-gradient(
  0,
  #48ded4 0%,
  #a026bf 20%,
  #e82c75 60%,
  $yellow 85%,
  #48ded4 95%
);
$color-white: white;
$color-cta: #e8d223; // #45ebad;
$color-main: #2a164b;
$color-grey: lighten(grey, 40%);
$color-black: #181818;
$color-vertical-overlay: linear-gradient(
  to bottom right,
  rgba($color-main, 0),
  $color-main 61%
);



