input,
textarea {
  width: 100%;
  max-width: 900px;
  padding: 10px;
  color: $color-white;
  background: #3c295d;
  border: none;
  border-left: 1px solid rgba(lighten($color-main, 20), 0.5);
  border-top: 1px solid rgba(lighten($color-main, 20), 0.5);
  box-shadow: 5px 5px 32px rgba($color-black, 0.2);
  outline: none;

  &.button {
    max-width: 300px;
    font-weight: bold;
    margin-top: 24px;
    display: block;
    @include button-style($color-main, $color-cta);

    @media (max-width: 700px) {
      margin: 24px auto 0;
    }
  }

  &.input {
    &--error {
      border-color: red;
    }
  }
}

textarea {
  min-height: 150px;
}

label {
  padding: 10px 0;
  display: block;
}

form {
  padding-left: 30px;
  @media screen and(max-width: 950px) {
    padding: 0 24px;
  }

  &.card {
    max-width: 900px;
    width: 100%;
    margin: 0 auto;
    padding: 0;

    @media screen and(max-width: 950px) {
      padding: 0 24px;
    }
  }
}

input.button.update-account {
  display: inline-block;
  margin-top: 24px;
  margin-left: 12px;
  border: none;
  @include button-style($color-white, lighten($color-main, 5));
  @media screen and(max-width: 669px) {
    margin: 24px 0 0 0;
    max-width: 100%;
  }
}

.logout-button {
  display: inline-block;
  padding: 10px;
  width: 100%;
  max-width: 300px;
  font-weight: bold;
  text-align: center;
  @include button-style($color-white, lighten($color-main, 5));

  @media screen and(max-width: 669px) {
    margin: 24px 0 0 0;
    max-width: 100%;
  }
}

.form {
  width: 100%;
  max-width: 900px;
  padding: 2rem;
  margin: 0 auto;

  & + .form {
    margin-top: 4rem;
  }

  h2 {
    margin: 0;
    font-size: 4rem;
    padding-bottom: 2rem;
  }
}

.hiddenForm {
  display: inherit;
  height: 0;
  padding: 0;
  opacity: 0;
}

.photo-label {
  input {
    display: block;
  }
}

::-webkit-input-placeholder {
  color: rgba($color-white, 0.5);
}
::-moz-placeholder {
  color: rgba($color-white, 0.5);
}
:-ms-input-placeholder {
  color: rgba($color-white, 0.5);
}
:-moz-placeholder {
  color: rgba($color-white, 0.5);
}



