.mobile {
  .content {
    margin-left: auto;
    width: 100vw;
  }
  .title--single,
  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    width: calc(100% - 12px);
    margin-left: 12px;
  }

  h2 {
    &.quests--layout-option {
      display: none;
    }
  }

  .quest__action--heart,
  .complete-quest,
  .edit-quest {
    width: 33%;
    display: flex;
    justify-content: center;
  }
  .complete-quest__icon {
    margin-top: 6px;
  }

  #map {
    left: 0;
    height: 100vw;
  }

  .form {
    width: calc(100% - 24px);
    padding: 0;
  }

  .quest {
    border: none;
    &:hover {
      .quest__info:after {
        display: none;
      }
    }
    &__details {
      .title {
        margin: 12px 24px;
        text-shadow: 3px 2px 6px rgba($color-black, 1);
      }
      .quest__info {
        margin: 0 24px;
        color: white;
      }
    }
    &__hero {
      .quest__action--heart {
        margin-left: 12px;
      }
      &--first {
        height: 210px;
        background-size: cover;
        margin-top: -14px;
      }
    }
  }
  .tags {
    &.tags--city-page {
      width: 100%;
      margin-left: 0;
      padding-bottom: 12px;
    }
  }
}



