@import url("https://fonts.googleapis.com/css?family=Montserrat:300,400,700");

@font-face {
  font-family: "panama";
  src: url("/fonts/panama-bold-webfont.woff2") format("woff2"),
    url("/fonts/panama-bold-webfont.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

html {
  font-size: 10px;
}

body {
  font-size: 1.5rem;
  color: $color-white;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  position: relative;
  font-size: 4rem;
  font-family: "Montserrat", sans-serif;
  font-weight: 600;
  letter-spacing: -1px;
  color: $color-white;
}

p,
span,
a {
  font-family: "Montserrat", sans-serif;
}

a {
  text-decoration: none;
  color: $color-cta;
}

p {
  line-height: 2;
  font-size: 1.6rem;

  @media screen and(max-width: 950px) {
    font-size: 1.5rem;
  }
  @media screen and(max-width: 500px) {
    font-size: 1.4rem;
    line-height: 1.5;
  }
}

.title {
  word-wrap: break-word;
  position: relative;
  z-index: 2;
  margin: 0;
  font-size: 2rem;
  line-height: 1.1;
  &--long {
    font-size: 30px;
  }

  line-height: 1.1;
  // inline link inside
  a {
    border-bottom: 0;
  }
}



